import React from 'react';
import Table from '../components/Table';
import NavBar from '../components/NavBar';

class Customer extends React.Component {

    constructor(props) {
        super(props)

        //默认显示manage users界面 
        this.state = {  
            tableType: 'Deliveries', 
            tableTr: ['Delivery_ID', 'Target_Box', 'Assigned_Deliverer', 'Deliverer_Token', 'Status','QR code'],
            tableContent: null
        };       
    }


    _onClickItem(tableTextContent, tableTrContent) {
        //传递数据到table显示组件
        this.setState({
            tableType: tableTextContent,
            tableTr: tableTrContent
            //pass tableContent
        });     
    }


    render() {
        const userRole = 'Customer'
        const tableType = ['Boxes', 'Deliveries'];
    
        const tabelTrDelivery = ['Delivery_ID', 'Target_Box', 'Assigned_Deliverer', 'Deliverer_Token', 'Status','QR code'];
        const tabelTrBox = ['RFID', 'Box_Name', 'Street_Name', 'Assigned_Customer', 'Status', 'Deliveries_Status'];

        return (     
            <div className="sb-nav-fixed">
                <NavBar />
                <div id="layoutSidenav">
                    <div id="layoutSidenav_nav">
                        <nav className="sb-sidenav accordion sb-sidenav-dark" id="sidenavAccordion">
                            <div className="sb-sidenav-menu">
                                <div className="nav">
                                    <div className="sb-sidenav-menu-heading">Side Navigation</div>
                                    <a className="nav-link collapsed" href="#" data-bs-toggle="collapse" data-bs-target="#collapseLayouts" aria-expanded="false" aria-controls="collapseLayouts">
                                        <div className="sb-nav-link-icon"><i className="fas fa-columns"></i></div>
                                            Roles
                                        <div className="sb-sidenav-collapse-arrow"><i className="fas fa-angle-down"></i></div>
                                    </a>
                                    <div className="collapse" id="collapseLayouts" aria-labelledby="headingOne" data-bs-parent="#sidenavAccordion">
                                        <nav className="sb-sidenav-menu-nested nav">
                                            <a className="nav-link" onClick={() =>{this._onClickItem(tableType[0], tabelTrBox)}} >Manage Boxes</a>
                                            <a className="nav-link" onClick={() =>{this._onClickItem(tableType[1], tabelTrDelivery)}}>Manage Deliveries</a>
                                        </nav>
                                    </div>     
                                </div>
                            </div>
                            <div className="sb-sidenav-footer">
                                <div className="small">Logged in as:</div>
                                 {userRole}
                            </div>
                        </nav>
                    </div>
                    <div id="layoutSidenav_content">
                        <main>
                            <div className="container-fluid px-4">
                                <h1 className="mt-4">Welcome {userRole} !</h1>
                                <ol className="breadcrumb mb-4">
                                    <li className="breadcrumb-item"><a href="index.html">Dashboard</a></li>
                                    <li className="breadcrumb-item active">Tables</li>
                                </ol>
                                <div className="card mb-4">
                                    <div className="card-body">
                                        You could view/add/edit/delete all the current {this.state.tableType} in the following table.
                                    </div>
                                </div>
                                <div className="card mb-4">
                                    <div className="card-header">
                                        <i className="fas fa-table me-1"></i>
                                            Manage {this.state.tableType}
                                    </div>
                                    <div className="card-body">
                                        <Table tr={this.state.tableTr}/>
                                    </div>
                                    
                                </div>
                            </div>
                        </main>
                        <footer className="py-4 bg-light mt-auto">
                            <div className="container-fluid px-4">
                                <div className="d-flex align-items-center justify-content-between small">
                                    <div className="text-muted">Copyright &copy; Your Website 2023</div>
                                    <div>
                                        <a href="#">Privacy Policy</a>
                                        &middot;
                                        <a href="#">Terms &amp; Conditions</a>
                                    </div>
                                </div>
                            </div>
                        </footer>
                    </div>
                </div>
            </div>
        )

    }
}

export default Customer
