import { useEffect } from 'react'
import { Outlet, useNavigate, useLocation } from "react-router-dom";
import { getLoginState } from "../utils";
import { ROLE } from "../constants";

function Layout() {
    const navigate = useNavigate();
    const location = useLocation();

    // call when the layout component is rendered
    useEffect(() => {
        const loginState = getLoginState();
        if (loginState && loginState.role) {
            // redirect
            if ("/" + loginState.role !== location.pathname) {
                switch (loginState.role) {
                    case ROLE.DISPATCHER:
                        navigate('/dispatcher');
                        break;
                    case ROLE.DELIVERER:
                        navigate('/deliverer');
                        break;
                    case ROLE.CUSTOMER:
                        navigate('/customer');
                        break;
                    default:
                        navigate('/login', {replace: true});
                        break;
                }
            }
        } else {
            // set replace to true so no history is recorded
            navigate('/login', {replace: true});
        }
    }, []);

    return (
        <div>
            Layout
            <Outlet/>
        </div>
    );
}

export default Layout
